<template>
  <div id="app">
    <el-menu mode="horizontal">
      <el-menu-item index="1">FightingGame.community</el-menu-item>
      <el-menu-item index="2">
        <el-dropdown>
          <span class="el-dropdown-link">Scenes</span>

          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-if="scenes" v-for="scene in scenes" :key="scene.name">{{ scene.name }}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-menu-item>
      <el-menu-item index="3">
        <el-dropdown>
          <span class="el-dropdown-link">Games</span>

          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-if="games" v-for="game in games" :key="game.name">{{ game.name }}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-menu-item>
      <el-menu-item index="4">
        <el-dropdown>
          <el-input class="el-dropdown-link" v-model="search"></el-input>

          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-for="result in results" :key="result.name">{{ result.name }}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
    </el-menu-item>
    </el-menu>
    <div class="map-container">
      <mapbox class="map"></mapbox>
    </div>
  </div>
</template>

<script>
import request from 'superagent'
import mapbox from './components/map'
export default {
  name: 'App',
  components: { mapbox },
  data () {
    return {
      search: '',
      scenes: false,
      games: false,
      results: false
    }
  },
  mounted () {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((result) => {
        console.log(result)
      })
      var vm = this
      console.log(vm)
      request(`http://localhost:3000/scenes`)
        .then((res) => {
          console.log(res.body)
          vm.scenes = res.body
        })
      request(`http://localhost:3000/games`)
        .then((res) => {
          console.log(res.body)
          vm.games = res.body
        })
    }
  },
  watch: {
    search () {
      var vm = this
      var gamesPromise = request(`http://localhost:3000/games?search=${this.search}`)
        .then((res) => res.body)
      var scenesPromise = request(`http://localhost:3000/scenes?search=${this.search}`)
        .then((res) => res.body)
      Promise.all([gamesPromise, scenesPromise]).then((resultArrays) => {
        var gamesPromise = resultArrays[0]
        if (gamesPromise == null) {
          gamesPromise = []
        }
        var scenesPromise = resultArrays[1]
        if (scenesPromise == null) {
          scenesPromise = []
        }
        vm.results = gamesPromise.concat(scenesPromise)
      })
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

.map-container {
  display: flex;
  flex-flow: column;
  height: 90vh;
}

.menu {
  height: 10vh;
}

.map {
  flex: 2;
  width: 100%;
}

el-dropdown-menu-item {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
}
</style>



